.container_detalhesP{
    position: fixed;
    opacity: 0;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(45, 45, 45, 0.4);
    backdrop-filter: blur(15px);
    z-index: -10;
}
.container_detalhesP.open{
    opacity: 1;
    z-index: 10;
}
.painel_detalhes{
    z-index: 101;
    position: fixed;
    opacity: 0;
    background-color: rgba(60, 60, 60, 0.85);
    color: rgba(235, 235, 235, 0.9);
    left: 15%;
    top: 10%;
    width: 70%;
    height: 80%;
    border-radius: 30px;
    transform: scale(0.8);
    transition: all 0.5s ease-in-out;
}
.painel_detalhes.open{
    transform: scale(1);
    opacity: 1;
    left: 15%;
    top: 10%;
    width: 70%;
    height: 80%;
}
.btn_fechar{
    background-color: transparent;
    font-size: 18pt;
    border: 0;
    cursor: pointer;
    position: fixed;
    top: 50px;
    right:50px;
    width: 50px;
    height: 50px;
    color: rgb(160, 160, 160);
    z-index: 9999;
    text-align: center;
}
.btn_fechar:hover{
    color: White;
}
.conteudo_detalhes{
    padding: 40px 150px 40px 150px;
}
.img_detalhes1{
    position: relative;
    padding-top: 4%;
    cursor: zoom-in;
    max-width: 100%;
    max-height: 100%;
    width: 60%;
    left: 20%;
    height: 55%;
    transition: all 0.3s ease-in-out;
}
.img_detalhes1.zoom{
    cursor: zoom-out;
    transform: scale(2);
}

/* responsividade */

@media only screen and (max-width: 600px) {
 
.painel_detalhes.open{
    left: 5%;
    top: 5%;
    width: 90%;
    height: 90%;
}
.img_detalhes1{
    width: 96%;
    padding-top: 7%;
    left: 2%;
    height: 25%;
    transition: all 0.3s ease-in-out;
}
.btn_fechar{
    font-size: 16pt;
    width: 40px;
    height: 40px;
    top: 0px;
    right:0px;
}
.conteudo_detalhes{
    padding: 10px 40px 10px 40px;
}
}